<template>
  <div id="app">
    <el-container style="height:100%">
      <el-header class="admin-header box-shadow">
        <app-header></app-header>
      </el-header>
      <el-container>
        <el-aside class="admin-side" style="width:200px">
              <app-side></app-side>
        </el-aside>
        <el-main class="admin-main">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import side from '@/components/common/side';
import header from '@/components/common/header';
export default {
  name: "app",
  components:{
      "app-side":side,
      "app-header":header
		}
};
</script>


